<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yangzhiquyu" >
					<el-input v-model="ruleForm.yangzhiquyu" placeholder="" clearable  :readonly="ro.yangzhiquyu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yangzhiquyu" >
					<el-input v-model="ruleForm.yangzhiquyu" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.tupian" label="" prop="tupian" >
					<file-upload
						tip=""
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.tupian?ruleForm.tupian:''"
						@change="tupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.tupian" label="" prop="tupian" >
					<img v-if="ruleForm.tupian.substring(0,4)=='http'&&ruleForm.tupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian" width="100" height="100">
					<img v-else-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="select" v-if="type!='info'"  label="" prop="yangzhipinzhong" >
					<el-select :disabled="ro.yangzhipinzhong" v-model="ruleForm.yangzhipinzhong" placeholder="" >
						<el-option
							v-for="(item,index) in yangzhipinzhongOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yangzhipinzhong" >
					<el-input v-model="ruleForm.yangzhipinzhong"
						placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yangzhishuliang" >
					<el-input v-model.number="ruleForm.yangzhishuliang" placeholder="" clearable  :readonly="ro.yangzhishuliang"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yangzhishuliang" >
					<el-input v-model="ruleForm.yangzhishuliang" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="shengzhangjieduan" >
					<el-input v-model="ruleForm.shengzhangjieduan" placeholder="" clearable  :readonly="ro.shengzhangjieduan"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="shengzhangjieduan" >
					<el-input v-model="ruleForm.shengzhangjieduan" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="toumiaoshijian" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.toumiaoshijian" 
						type="date"
						:readonly="ro.toumiaoshijian"
						:picker-options="toumiaoshijianPickerOptions"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.toumiaoshijian" label="" prop="toumiaoshijian" >
					<el-input v-model="ruleForm.toumiaoshijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="" prop="yujishouhuoshijian" >
					<el-date-picker
						format="yyyy  MM  dd "
						value-format="yyyy-MM-dd"
						v-model="ruleForm.yujishouhuoshijian" 
						type="date"
						:readonly="ro.yujishouhuoshijian"
						:picker-options="yujishouhuoshijianPickerOptions"
						placeholder=""
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.yujishouhuoshijian" label="" prop="yujishouhuoshijian" >
					<el-input v-model="ruleForm.yujishouhuoshijian" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					yangzhiquyu : false,
					tupian : false,
					yangzhipinzhong : false,
					yangzhishuliang : false,
					shengzhangjieduan : false,
					toumiaoshijian : false,
					yujishouhuoshijian : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					sfsh : false,
					shhf : false,
				},
			
				ruleForm: {
					yangzhiquyu: '',
					tupian: '',
					yangzhipinzhong: '',
					yangzhishuliang: '',
					shengzhangjieduan: '',
					toumiaoshijian: '',
					yujishouhuoshijian: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
					shhf: '',
				},
				yangzhipinzhongOptions: [],

				toumiaoshijianPickerOptions: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				yujishouhuoshijianPickerOptions: {
					disabledDate(time) {
						return time.getTime() < Date.now() - 8.64e7;
					}
				},
				rules: {
					yangzhiquyu: [
					],
					tupian: [
					],
					yangzhipinzhong: [
					],
					yangzhishuliang: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					shengzhangjieduan: [
					],
					toumiaoshijian: [
					],
					yujishouhuoshijian: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					sfsh: [
					],
					shhf: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='yangzhiquyu'){
							this.ruleForm.yangzhiquyu = obj[o];
							this.ro.yangzhiquyu = true;
							continue;
						}
						if(o=='tupian'){
							this.ruleForm.tupian = obj[o];
							this.ro.tupian = true;
							continue;
						}
						if(o=='yangzhipinzhong'){
							this.ruleForm.yangzhipinzhong = obj[o];
							this.ro.yangzhipinzhong = true;
							continue;
						}
						if(o=='yangzhishuliang'){
							this.ruleForm.yangzhishuliang = obj[o];
							this.ro.yangzhishuliang = true;
							continue;
						}
						if(o=='shengzhangjieduan'){
							this.ruleForm.shengzhangjieduan = obj[o];
							this.ro.shengzhangjieduan = true;
							continue;
						}
						if(o=='toumiaoshijian'){
							this.ruleForm.toumiaoshijian = obj[o];
							this.ro.toumiaoshijian = true;
							continue;
						}
						if(o=='yujishouhuoshijian'){
							this.ruleForm.yujishouhuoshijian = obj[o];
							this.ro.yujishouhuoshijian = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
					}
				}
				// 
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!=""){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
							this.ro.yonghuzhanghao = true;
						}
						if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!=""){
							this.ruleForm.yonghuxingming = json.yonghuxingming
							this.ro.yonghuxingming = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
				this.$http({
					url: `option/yangzhipinzhong/yangzhipinzhong`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.yangzhipinzhongOptions = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 

			info(id) {
				this.$http({
					url: `yangzhijihua/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//
						let reg=new RegExp('../../../upload','g')//g
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 
			async onSubmit() {
					if(this.ruleForm.tupian!=null) {
						this.ruleForm.tupian = this.ruleForm.tupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					if(!this.ruleForm.id) {
						delete this.ruleForm.userid
					}
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `yangzhijihua/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.yangzhijihuaCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.yangzhijihuaCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			tupianUploadChange(fileUrls) {
				this.ruleForm.tupian = fileUrls;
			},
		}
	};
</script>
<style lang="scss" scoped>
