			init () {
				let urlwendu = 'yangzhihuanjing/remind/wendu/1';
				urlwendu+=`?remindend=5`,
				
				urlwendu+=`&remindstart=35`,

				this.$http({
					url: urlwendu,
					method: "get",
				}).then(({ data }) => {
					if (data && data.code == 0 && data.count>0 ) {
						const h = this.$createElement;
						var name = data.data.join(',')
						this.$msgbox({
							message: h('p', null, [
								h('div', { style: 'text-align: center;font-weight: bold;font-size: 20px;padding: 20px 0' }, ``),
								h('p', null, [
									h('div', { style: 'text-align: center;font-weight: bold;padding: 0 0 10px;font-size: 16px' }, `${data.count}`),
									h('div', { style: 'text-align: center' }, [
										h('span', null, ``),
										h('span', { style: 'color: teal;font-weight: bold' }, `${name}`)
									])
								])
							]),
							showCancelButton: false,
						})
					}
				});
